import React from 'react';
import { SuperAgentIcon } from '../../Icons/SuperAgentIcon/SuperAgentIcon';

const HorizontalLogo = ({width}) => (
    <SuperAgentIcon
        sx={{
            paddingLeft: '20px',
            paddingRight: '20px',
            maxWidth: width,
            width: '100%',
            height: '100%',
        }}
    />
);
export default HorizontalLogo
