import { useTranslation } from 'react-i18next';
import Grid from '@mui/material/Grid';
import { useUsageMetrics } from '../Hook/useUsageMetrics';
import DataCard from './DataCard';

function UsageMetrics() {
    const { t } = useTranslation();
    const { metricsData } = useUsageMetrics();

    return (
        <Grid container justifyContent="center" spacing={2}>
            <Grid item xs={4} md={4}>
                <DataCard
                    number={metricsData.websitesVisited}
                    description={t('options.dashboard.summary.grid.websites_visited.description')}
                    info={t('options.dashboard.summary.grid.websites_visited.info')}
                    itemsMinWidth={false}
                />
            </Grid>
            <Grid item xs={4} md={4}>
                <DataCard
                    number={metricsData.totalClicks}
                    description={t('options.dashboard.summary.grid.total_clicks.description')}
                    info={t('options.dashboard.summary.grid.total_clicks.info')}
                    itemsMinWidth={false}
                />
            </Grid>
            <Grid item xs={4} md={4}>
                <DataCard
                    number={metricsData.timeSaved.toFixed(metricsData.timeSaved >= 10_000 ? 0 : 1)}
                    description={t('options.dashboard.summary.grid.time_saved.description')}
                    info={t('options.dashboard.summary.grid.time_saved.info')}
                    itemsMinWidth={false}
                />
            </Grid>
        </Grid>
    );
}

export default UsageMetrics;
