import {useCallback, useEffect, useState} from 'react';
import { Messages, Request } from '../../../shared';

export const useUsageMetrics = () => {
    const [loading, setLoading] = useState(true);
    const [metricsData, setMetricsData] = useState({
        websitesVisited: 0,
        totalClicks: 0,
        timeSaved: 0
    });

    const loadUsageMetrics = useCallback(() => {
        new Request(Messages.Logger.GetUsageMetrics).sendRequest()
            .then((results) => {
                const metrics = results.message.payload.metrics;
                setMetricsData({
                    websitesVisited: metrics.ws,
                    totalClicks: metrics.cc,
                    timeSaved: metrics.ms
                });
                setLoading(false);
            })
            .catch(() => {});
    }, []);

    useEffect(() => {
        loadUsageMetrics();
    }, [loadUsageMetrics]);

    return {
        loading,
        metricsData,
    }
}
