import React, { useCallback } from 'react';
import Button from '@mui/material/Button';
import { Messages, Request } from '../../../../shared';

export default function SubscribeButton() {
    const onClick = useCallback(async () => {
        await new Request(Messages.Generic.AppOpen, {}).sendRequest();
        window.location.replace('superagent:subscribe');
    }, []);

    return (
        <Button
            variant="contained"
            color="primary"
            onClick={onClick}
        >
            Subscribe Now
        </Button>
    );
}
