// React
import React, { useEffect } from 'react';

// Owned
import Welcome from './Components/Welcome/Welcome';
import { SAAnalytics } from '../../Background/Analytics/SAAnalytics';
import { CoreApp } from '../Shared/Components/CoreApp/CoreApp';
import CssBaseline from '@mui/material/CssBaseline';

function App() {
    useEffect(() => {
        SAAnalytics.recordPageOpened('Subscribe');
    }, []);

    return (
        <CoreApp>
            <CssBaseline/>
            <Welcome/>
        </CoreApp>
    );
}

export default App;
