// External
import React, { useState, useEffect } from 'react';

// Material UI
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';
import CircularProgress from '@mui/material/CircularProgress';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';

// Owned
import { useStyles } from '../styles';
import HorizontalLogo from '../../../Shared/Components/HorizontalLogo/HorizontalLogo';
import SubscribeButton from './SubscribeButton';
import UsageMetrics from '../../../Shared/View/UsageMetrics';

export default function Welcome() {
    const classes = useStyles();

    const [loading, setLoading] = useState(false);

    useEffect(() => {
        setLoading(false);
    }, []);

    return(
        <Container component="main" maxWidth="md">
            {loading ?
                <div className={classes.paper}>
                    <CircularProgress />
                </div>
            :
                <div className={classes.welcomeHeader}>
                    <div className={classes.imgHeader}>
                        <HorizontalLogo width="300px" />
                    </div>
                    <Grid container spacing={3}>
                        <Grid item xs={12}>
                            <Box>
                                <SubscribeButton />
                            </Box>
                        </Grid>
                        <Grid item xs={12}>
                            <UsageMetrics />
                        </Grid>
                        <Grid item xs={12}>
                            <Box>
                                <Typography variant="body" sx={{
                                    marginTop: "20px"
                                }}>
                                    Hi there! 
                                    <br />
                                    <br />
                                    Thank you for using superagent. 
                                    We're glad you're finding it helpful! In order to keep superagent up and running smoothly, 
                                    we currently charge iOS devices a subscription fee of $1.19 per month, $11.99 a year or a 
                                    lifetime access for $29.99.

                                    <br/>
                                    <br/>

                                    <b>superagent will still work up to 3 times a day in the free version.</b>
                                    

                                    <br />
                                    <br />
                                    You can subscribe by opening the superagent app, going to Settings and Manage Subscriptions.
                                    <br />
                                    <br />
                                    
                                    We won't bother you with this message again for a while, 
                                    but we wanted to let you know that we really appreciate your support as a user of superagent.
                                    If you do decide to subscribe, you'll get unlimited access to all of superagent's present and future features.
                                    If not, no hard feelings! 
                                    
                                    <br />
                                    <br />
                                    
                                    Thank you for being a part of the superagent community.
                                </Typography>
                            </Box>
                        </Grid>
                        <Grid item xs={12}>
                            <Box>
                                <SubscribeButton />
                            </Box>
                        </Grid>
                    </Grid>
                </div>            
            }

        </Container>
    )
}
